.AppHeader {
    height: 100px;
    padding: 20px;
    font-weight: bold;
    color: antiquewhite;
    font-family: HemeraIi, serif;
}
.searchForm{
    min-height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.searchBar{
    width: 50%;
    border: none;
    padding: 10px;
}
input:focus { border-color: crimson }
input:active{ border-color: lightgreen }
input:focus:hover { border-color: aqua }
.searchButton{
    background: darkcyan;
    border: none;
    padding: 10px 20px;
    color: white;
}
.recipes{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
